import React from 'react'
import { func, string } from 'prop-types';
import styled from 'styled-components';
import { ReactComponent as MoonIcon } from "../../assests/font-awesome/svgs/regular/moon.svg"
import { ReactComponent as SunIcon } from "../../assests/font-awesome/svgs/regular/sun.svg"
import "./Toggle.css"

const ToggleContainer = styled.button`
  background: ${({ theme }) => theme.gradient};
  border: 2px solid ${({ theme }) => theme.toggleBorder};
  border-radius: 30px;
  cursor: pointer;
  display: flex;
  font-size: 0.5rem;
  justify-content: space-between;
  align-items : center;
  margin: 0.9rem auto;
  overflow: hidden;
  padding: 0.3rem 0.3rem;
  position: relative;
  width: 6rem;
  height: 2rem;

  svg {
    height: auto;
    width: 1.5rem;
    transition: all 0.3s linear;
    
    // sun icon
    &:first-child {
      transform: ${({ lightTheme }) => lightTheme ? 'translateY(0)' : 'translateY(100px)'};
    }
    
    // moon icon
    &:nth-child(2) {
      transform: ${({ lightTheme }) => lightTheme ? 'translateY(-100px)' : 'translateY(0)'};
    }
  }
`;
const Toggle = ({ theme, themeMode, toggleTheme }) => {
  const lightTheme = themeMode === 'light';
  return (
    <ToggleContainer theme = {theme} lightTheme={lightTheme} onClick={toggleTheme} >
      <SunIcon />
      <MoonIcon />
    </ToggleContainer>
  );
};

// Toggle.propTypes = {
//   theme: string.isRequired,
//   toggleTheme: func.isRequired,
// }

export default Toggle;